.home {
	width: 100%;
	min-height: 100vh;
	@include background('../../assets/home/background-home-desktop.jpg');

	padding-top: 20vh;
	@media only screen and (max-width: 768px) {
		height: auto;
		@include background('../../assets/home/background-home-tablet.jpg');
	}
	@media only screen and (max-width: 550px) {
		padding-top: 15vh;
		@include background('../../assets/home/background-home-mobile.jpg');
	}

	&__main {
		width: 80%;
		margin: 150px auto 0;
		@include flex(flex-end);
		@media only screen and (max-width: 768px) {
			align-items: center;
			flex-direction: column;
			margin: 4vw 0 0;
			width: 100%;
		}

		.home__text--container {
			width: 50%;
			text-transform: uppercase;
			color: $white;
			@media only screen and (max-width: 768px) {
				width: 90%;
				text-align: center;
			}

			.home--subheading {
				font-size: $h5;
				letter-spacing: 4.72px;
				color: $gray;
				font-weight: 300;
				opacity: 0;
				position: relative;
				animation: text 250ms ease-in-out 1000ms 1 forwards;
				@media only screen and (max-width: 768px) {
					letter-spacing: 0.4vw;
					font-size: 3vw;
				}
				@media only screen and (max-width: 550px) {
					font-size: 4vw;
				}
			}
			.home--heading {
				font-size: $h1;
				margin: 20px 0;
				font-weight: 300;
				opacity: 0;
				position: relative;
				animation: text 250ms ease-in-out 1250ms 1 forwards;
				@media only screen and (max-width: 768px) {
					margin: 2vw 0;
					font-size: 25vw;
				}
				@media only screen and (max-width: 550px) {
					margin: 4vw 0;
					font-size: 28vw;
				}
			}
			.home--para {
				font-family: $font-sans;
				font-size: 18px;
				width: 75%;
				color: $gray;
				text-align: justify;
				line-height: 2rem;
				font-weight: 200;
				letter-spacing: 0.5px;
				text-transform: lowercase;
				opacity: 0;
				position: relative;
				animation: text 250ms ease-in-out 1500ms 1 forwards;
				@media only screen and (max-width: 768px) {
					width: 60%;
					text-align: center;
					line-height: 4vw;
					margin: 0 auto;
				}
				@media only screen and (max-width: 550px) {
					width: 90%;
					line-height: 7vw;
				}
			}
		}
		a {
			text-decoration: none;
		}
		.home__explore--container {
			$size: 200px;
			width: $size;
			height: $size;

			display: flex;
			align-items: center;
			justify-content: center;

			background: $white;
			box-shadow: 0 0 10px rgba(0, 0, 0, 0.2), 0 0 40px rgba(0, 0, 0, 0.15);
			border-radius: 50%;
			border: none;
			outline: none;
			cursor: pointer;

			transition: 500ms ease-in-out;
			animation: explore 1000ms ease-in-out;
			@media only screen and (max-width: 768px) {
				$size: 25vw;
				width: $size;
				height: $size;
				margin: 10vw 0 5vw;
			}
			@media only screen and (max-width: 550px) {
				$size: 40vw;
				width: $size;
				height: $size;
				margin: 20vw 0 15vw;
			}
			@keyframes explore {
				0% {
					transform: scale(0);
					opacity: 0;
				}
				50% {
					transform: scale(1.15);
					opacity: 1;
				}
				100% {
					transform: scale(1);
				}
			}

			&:hover {
				box-shadow: 0 0 1px 100px rgba(255, 255, 255, 0.1);
				@media only screen and (max-width: 768px) {
					box-shadow: 0 0 1px 10vw rgba(255, 255, 255, 0.1);
				}
			}

			span {
				font-family: $font-serif;
				font-size: $h5;
				font-weight: 300;

				color: $black;
				letter-spacing: 1px;
				text-transform: uppercase;
				@media only screen and (max-width: 768px) {
					font-size: 3vw;
				}
				@media only screen and (max-width: 550px) {
					font-size: 6vw;
				}
			}
		}
	}
}

@keyframes text {
	from {
		top: -20px;
		opacity: 0;
	}
	to {
		top: 0;
		opacity: 1;
	}
}
